<template>
	<el-form ref="form" :model="form" label-width="80px" size="mini">
		<el-form-item label="用户名">
			<el-input v-model="form.username"></el-input>
		</el-form-item>
		<el-form-item label="用户头像">
			<el-input type="hidden" v-model="form.portrait"></el-input>
			<el-upload
					class="avatar-uploader"
					action="http://127.0.0.1/auth/upload"
					:show-file-list="false"
					:with-credentials="true"
					:on-success="handleUploadSuccess"
					:before-upload="beforeAvatarUpload">
				<img v-if="form.portrait" :src="form.portrait" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
		</el-form-item>
		<el-form-item>
			<el-button type="primary" @click="onSubmit">立即创建</el-button>
			<el-button>取消</el-button>
		</el-form-item>
	</el-form>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          username: '',
          portrait: ''
        }
      }
    },
    methods: {
      onSubmit() {
        this.$httpPost("admin/userAdd", this.form).then((response) => {
          if (response.code === 0) {
            this.$message({
              message: response.msg,
              center: true,
              type: "success"
            });
            this.form.username = ''
            this.form.portrait = ''
          } else {
            this.$message({
              message: response.msg,
              center: true,
              type: "warning"
            });
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
      handleUploadSuccess(res, file){
				console.log(file)
        if(res.code === 0) {
          this.form.portrait = res.data.file;
				}

			},
      beforeAvatarUpload(file){
        const isLt1M = file.size <  1024 * 1024;
        if (!isLt1M) {
          this.$message.error('上传头像图片大小不能超过1MB!');
        }
        return isLt1M;
			}
    }
  }
</script>

<style scoped>
	.avatar-uploader .el-upload {
		border: 1px solid #666;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}
	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}
	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 150px;
		height: 150px;
		line-height: 150px;
		text-align: center;
	}
	.avatar {
		width: 150px;
		height: 150px;
		display: block;
	}
</style>